body {
  background-color: #eee;
  margin: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.card {
  text-align: center;
  background-color: #fff;
  width: 168px;
  height: 408px;
  margin: auto;
}

.image-keeper {
  justify-content: center;
  display: flex;
}

.image {
  background-image: url("imac.202c52ff.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.title-keeper {
  justify-content: center;
  display: flex;
}

.title {
  color: #060b35;
  text-align: left;
  width: 166px;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.code-keeper {
  display: flex;
}

.code {
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.price-keeper {
  width: 166px;
  display: inline-block;
}

.price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.price__word {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price__value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.button-keeper {
  justify-content: center;
  align-items: center;
  width: 166px;
  display: flex;
}

.button {
  size: 14px;
  color: #fff;
  transition: background-color .5s liner;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  margin-top: 16px;
  padding: 12px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.review-keeper {
  justify-content: space-between;
  width: 168px;
  margin-top: 6px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 0;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: inline-block;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.reviews {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}
